<template>
  <div class="home-container">
    <el-scrollbar class="scrollbar-style" :style="{ height: '100vh' }">
      <div class="scrollbar-content-style">
        <header>
          <div class="title-content-box">
            <p class="title-level-one">{{ associationInfo.name }}</p>
            <p class="title-level-two">数智化管理平台</p>
          </div>
        </header>
        <!-- 主要内容区域 -->
        <main class="flex align-start">
          <!-- 左侧列表 -->
          <div>
            <ul class="list-style">
              <li class="list-item">
                <ListCard
                  title="近期活动区域列表"
                  :listData="recentPreachAreaListCmp"
                  :checkedList="preachingAreaCheckedList"
                  @changeCheck="onPreachingAreaChangeCheck"
                >
                  <TableList
                    :columnsList="recentPreachAreaListColumns"
                    :listData="recentPreachAreaListCmp"
                    overflow
                  />
                </ListCard>
              </li>
              <li class="list-item">
                <ListCard title="近期承办单位列表">
                  <TableList
                    :columnsList="recentUndertakingUnitListColumns"
                    :listData="recentUndertakingUnitListCmp"
                    overflow
                  />
                </ListCard>
              </li>
              <li class="list-item">
                <ListCard title="近期活动详情">
                  <TableList
                    :columnsList="recentAboutDetailsColumns"
                    :listData="recentAboutDetailsCmp"
                    overflow
                  />
                </ListCard>
              </li>
            </ul>
          </div>

          <!-- 中心地图 -->
          <div class="main-box">
            <TitleCard
              class="title-card-style"
              :title="`活动覆盖人次：${coverTotalNumber}人`"
            />
            <MapProjection :dataSource="mapProjection_DataSource" />
          </div>

          <!-- 右侧列表 -->
          <div>
            <ul class="list-style">
              <li class="list-item">
                <!-- :checkedList="coverListCheckedList" -->
                <ListCard
                  title="活动区域覆盖人次排名"
                  :radioList="coverListCheckedList"
                  @changeRadio="getCoverListCmp"
                >
                  <TableList
                    :columnsList="coverListColumns"
                    :listData="coverListCmp"
                    :showHeader="false"
                    overflow
                  />
                </ListCard>
              </li>
              <li class="list-item">
                <ListCard title="授课老师数据排名">
                  <TableList
                    :columnsList="instructorColumns"
                    :listData="instructorList"
                    overflow
                  />
                </ListCard>
              </li>
              <li class="list-item">
                <ListCard title="新增活动覆盖人次">
                  <CoverPeopleTotal
                    :dataSource="coverPeopleTotal_DataSource"
                    :xAxisData="coverPeopleTotal_xAxisData"
                  />
                </ListCard>
              </li>
            </ul>
          </div>
        </main>
      </div>
    </el-scrollbar>
  </div>
</template>

<script>
export default {
  components: {
    ListCard: () => import("./components/ListCard"),
    TableList: () => import("./components/TableList"),
    TitleCard: () => import("./components/TitleCard"),
    CoverPeopleTotal: () => import("./components/CoverPeopleTotal"),
    MapProjection: () => import("./components/MapProjection"),
  },
  computed: {
    /**
     * 近期活动区域列表
     */
    recentPreachAreaListCmp() {
      const arr = [];
      const province = this.preachingAreaCheckedList[0].checked;
      const city = this.preachingAreaCheckedList[1].checked;
      const district = this.preachingAreaCheckedList[2].checked;
      const street = this.preachingAreaCheckedList[3].checked;
      for (const item of this.recentList) {
        console.log(
          "区域 :>> ",
          `${item.province}${item.city}${item.district}${item.street}`
        );
        arr.push({
          areaName: `${province ? item.province : ""}${city ? item.city : ""}${
            district ? item.district : ""
          }${street ? item.street : ""}`,
          date: this.$dateFormat(item.start_date, "yyyy-mm-dd"),
        });
      }
      return arr;
    },
    /**
     * 近期承办单位列表
     */
    recentUndertakingUnitListCmp() {
      const arr = [];
      this.recentList.forEach((item) => {
        arr.push({
          ...item.host_info,
          date: this.$dateFormat(item.start_date, "UTC:yyyy-mm-dd"),
        });
      });
      console.log("近期承办单位列表arr :>> ", arr);
      return arr;
    },
    /**
     * 近期活动详情
     */
    recentAboutDetailsCmp() {
      const arr = [];
      this.recentList.forEach((item) => {
        arr.push({
          ...item.instructor_info,
          title: item.title,
          visitor: `${item.visitor}人`,
        });
      });
      console.log("近期活动详情arr :>> ", arr);
      return arr;
    },
    /**
     * 新增活动覆盖人次-图表数据
     */
    coverPeopleTotal_DataSource() {
      const arr = [];
      this.dateCoverList.forEach((item) => {
        arr.push(item.sum_visitor);
      });
      console.log("新增活动覆盖人次-数据arr :>> ", arr);
      return arr;
    },
    /**
     * 新增活动覆盖人次-X轴标签
     */
    coverPeopleTotal_xAxisData() {
      const arr = [];
      this.dateCoverList.forEach((item) => {
        arr.push(this.$dateFormat(item.g_date, "m/dd"));
      });
      console.log("新增活动覆盖人次-X轴标题arr :>> ", arr);
      return arr;
    },
    /**
     * 地图-图表数据
     */
    mapProjection_DataSource() {
      return this.handleLevel(this.associationInfo.model_type);
    },
    /**
     * 活动区域覆盖人次排名显示数据
     */
    // coverListCmp() {
    //   const arr = [];
    //   const city = this.coverListCheckedList[0].checked;
    //   const district = this.coverListCheckedList[1].checked;
    //   const street = this.coverListCheckedList[2].checked;
    //   console.log("this.coverListCheckedList :>> ", this.coverListCheckedList);
    //   for (const key in this.coverList) {
    //     arr.push({
    //       areaName: `${city ? this.coverList[key].city : ""}${
    //         district ? this.coverList[key].district : ""
    //       }${street ? this.coverList[key].street : ""}`,
    //       rankingName: `第${parseInt(key) + 1}名`,
    //       visitorStr: `${this.coverList[key].sum_visitor}人`,
    //     });
    //   }
    //   console.log("arr :>> ", arr);
    //   return arr;
    // },
  },
  data() {
    return {
      coverListCmp: [],
      // 近期活动区域列表columns
      recentPreachAreaListColumns: [
        {
          label: "区域名称",
          value: "areaName",
          align: "left",
        },
        {
          label: "日期",
          value: "date",
          width: 60,
        },
      ],
      // 近期承办单位列表columns
      recentUndertakingUnitListColumns: [
        {
          label: "单位名称",
          value: "name",
          align: "left",
        },
        {
          label: "日期",
          value: "date",
          width: 60,
        },
      ],
      // 近期活动详情columns
      recentAboutDetailsColumns: [
        {
          label: "活动主题",
          value: "title",
          align: "left",
        },
        {
          label: "主讲人",
          value: "name",
          width: 40,
        },
        {
          label: "听课人次",
          value: "visitor",
          width: 50,
          align: "right",
        },
      ],
      // 覆盖列表columns
      coverListColumns: [
        {
          label: "名次",
          value: "rankingName",
          width: 40,
        },
        {
          label: "覆盖区域名称",
          value: "areaName",
          align: "center",
        },
        {
          label: "区域覆盖人次",
          value: "visitorStr",
          width: 55,
          align: "right",
        },
      ],
      // 授课老师活动数据排名columns
      instructorColumns: [
        {
          label: "名次",
          value: "rankingName",
        },
        {
          label: "姓名",
          value: "name",
        },
        {
          label: "听课人次",
          value: "visitorStr",
          width: 48,
          align: "right",
        },
        {
          label: "场次",
          value: "roundStr",
          align: "right",
        },
      ],
      // 近期列表
      recentList: [],
      // 覆盖列表
      coverList: [],
      // 授课老师活动数据排名
      instructorList: [],
      // 日期覆盖列表
      dateCoverList: [],
      // 日期覆盖列表-市
      dateCoverCityList: [],
      // 日期覆盖列表-区
      dateCoverDistrictList: [],
      // 日期覆盖列表-街道
      dateCoverStreetList: [],
      // 覆盖总人次
      coverTotalNumber: 0,
      // 工会信息
      associationInfo: {},
      // 近期活动区域多选列表
      preachingAreaCheckedList: [
        {
          label: "省",
          value: "province",
          checked: true,
        },
        {
          label: "市",
          value: "city",
          checked: true,
        },
        {
          label: "区",
          value: "district",
          checked: true,
        },
        {
          label: "街道",
          value: "street",
          checked: true,
        },
      ],
      // 活动区域覆盖人次排名多选列表
      coverListCheckedList: [
        {
          label: "市",
          value: "city",
          checked: true,
        },
        {
          label: "区",
          value: "district",
          checked: false,
        },
        {
          label: "街道",
          value: "street",
          checked: false,
        },
      ],
    };
  },
  created() {
    // 近期活动区域多选列表
    this.preachingAreaCheckedList =
      this.$store.getters.preachingAreaCheckedList;
    // 活动区域覆盖人次排名多选列表
    this.coverListCheckedList = this.$store.getters.coverListCheckedList;
    // 根据域名获取工会信息
    this.$store.dispatch("association/associationInfoDomainAPI");
  },
  watch: {
    "$store.getters.associationInfo": function (newVal) {
      this.associationInfo = newVal;
      const aid = newVal.id;
      this.recentListAPI(aid);
      this.instructorListAPI(aid);
      this.dateCoverListAPI(aid);
      this.coverListCityAPI(aid);
      this.coverListDistrictAPI(aid);
      this.coverListStreetAPI(aid);
      this.coverTotalNumberAPI(aid);
    },
  },
  mounted() {
    this.associationInfo = this.$store.getters.associationInfo;
    // this.recentListAPI();
    // this.instructorListAPI();
    // this.dateCoverListAPI();
    // this.coverListCityAPI();
    // this.coverTotalNumberAPI();
    this.getCoverListCmp();
  },
  methods: {
    async getCoverListCmp(area="city") {
      const res = await this.$request("lecture.rankingPeople", {
        area: area,
      });
      this.coverListCmp = res.map((item, index) => {
        return {
          rankingName: `第${index + 1}名`,
          areaName: item.Title,
          visitorStr: item.SumVisitor,
        };
      });
    },
    /**
     * 近期列表API
     * @param {String} aid 机构ID
     */
    async recentListAPI(aid) {
      const data = {
        aid,
      };
      const res = await this.$request("lecture.recentList", data);

      console.log("近期列表API_res :>> ", res);

      res.forEach((item) => {
        item.date = this.$dateFormat(item.date, "yyyy-mm-dd");
        console.log("item.date :>> ", item.date);
      });

      this.recentList = res;
    },
    /**
     * 覆盖列表API
     */
    handleCoverList(coverList) {
      // const city = this.coverListCheckedList[0].checked;
      // const district = this.coverListCheckedList[1].checked;
      // const street = this.coverListCheckedList[2].checked;
      // for (const key in coverList) {
      //   coverList[key].areaName = `${city ? coverList[key].city : ""}${
      //     district ? coverList[key].district : ""
      //   }${street ? coverList[key].street : ""}`;
      //   coverList[key].rankingName = `第${parseInt(key) + 1}名`;
      //   coverList[key].visitorStr = `${coverList[key].sum_visitor}人`;
      // }

      console.log("覆盖列表API_res :>> ", coverList);
      this.coverList = coverList;
    },
    /**
     * 授课老师排名列表API
     */
    async instructorListAPI(aid) {
      const data = { aid };

      const res = await this.$request("lecture.instructorList", data);
      for (const key in res) {
        res[key].rankingName = `第${parseInt(key) + 1}名`;
        res[key].name = res[key].instructor_info
          ? res[key].instructor_info.name
          : "";
        res[key].visitorStr = `${res[key].sum_visitor}人`;
        res[key].roundStr = `${res[key].host_time}场`;
      }
      console.log("授课老师排名列表API_res :>> ", res);
      this.instructorList = res;
    },
    /**
     * 日期覆盖列表API
     */
    async dateCoverListAPI(aid) {
      const data = {
        type: "none",
        aid,
      };
      const res = await this.$request("lecture.dateCoverList", data);
      console.log("日期覆盖列表res :>> ", res);
      this.dateCoverList = res;
    },
    /**
     * 覆盖总人次API
     */
    async coverTotalNumberAPI(aid) {
      const data = { aid };
      const res = await this.$request("lecture.coverTotalNumber", data);
      console.log("覆盖总人次API_res :>> ", res);
      this.coverTotalNumber = res;
    },
    /**
     * 获取覆盖列表-市
     */
    async coverListCityAPI(aid) {
      const data = { aid };
      const res = await this.$request("lecture.coverListCity", data);
      console.log("res :>> ", res);
      this.dateCoverCityList = res;
    },
    /**
     * 获取覆盖列表-区
     */
    async coverListDistrictAPI(aid) {
      const data = { aid };
      const res = await this.$request("lecture.coverListDistrict", data);
      console.log("res :>> ", res);
      this.dateCoverDistrictList = res;
    },
    /**
     * 获取覆盖列表-街道
     */
    async coverListStreetAPI(aid) {
      const data = { aid };
      const res = await this.$request("lecture.coverListStreet", data);
      console.log("res :>> ", res);
      this.dateCoverStreetList = res;
    },

    /**
     * 处理地图层级数据
     * @param {String} level 区域层级 (province省 || city市 || district区)
     */
    handleLevel(level) {
      const arr = [];
      switch (level) {
        case "province":
          this.handleCoverList(this.dateCoverCityList);
          this.dateCoverCityList.forEach((item) => {
            if (item.city && item.city !== "") {
              arr.push({
                name: item.city,
                value: item.sum_visitor,
              });
            }
          });
          break;
        case "city":
          this.handleCoverList(this.dateCoverDistrictList);
          this.dateCoverDistrictList.forEach((item) => {
            if (item.district && item.district !== "") {
              arr.push({
                name: item.district,
                value: item.sum_visitor,
              });
            }
          });
          break;
        case "district":
          this.handleCoverList(this.dateCoverStreetList);
          this.dateCoverStreetList.forEach((item) => {
            if (item.street && item.street !== "") {
              arr.push({
                name: item.street,
                value: item.sum_visitor,
              });
            }
          });
          break;

        default:
          break;
      }

      console.log("地图-图表数据arr :>> ", arr);

      return arr;
    },
    /**
     * 数据显示控制--近期活动区域列表
     */
    onPreachingAreaChangeCheck(e, row) {
      for (const item of this.preachingAreaCheckedList) {
        if (item.value === row.value) {
          item.checked = e;
        }
      }

      this.$store.commit(
        "viewFilter/setPreachingAreaCheckedList",
        this.preachingAreaCheckedList
      );
    },
    /**
     * 数据显示控制--活动区域覆盖人次排名
     */
    onCoverListChangeCheck(e, row) {
      for (const item of this.coverListCheckedList) {
        if (item.value === row.value) {
          item.checked = e;
        }
      }

      this.$store.commit(
        "viewFilter/setCoverListCheckedList",
        this.coverListCheckedList
      );
    },
  },
};
</script>

<style lang="scss" scoped>
.home-container {
  width: 100vw;
  height: 100vh;
  // padding: px(28) px(39);
  // width: calc(100vw - px(39 * 2));
  // height: calc(100vh - px(28 * 2));
  background: border-box no-repeat center / 100% 100%
    url("~@/assets/images/BG.png");
  position: relative;
  overflow: hidden;

  .scrollbar-style {
    ::v-deep .el-scrollbar__wrap {
      overflow-x: hidden;
    }
    ::v-deep .el-scrollbar__bar {
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
    }

    .scrollbar-content-style {
      padding: px(28) px(39);
      overflow-x: hidden;

      header {
        margin: 0 auto;
        width: px(998);
        height: px(114);
        background: border-box no-repeat center / 100% 100%
          url("~@/assets/images/big_headline_box.png");
        text-align: center;
        .title-content-box {
          padding: px(4) 0 0;
          text-align: center;
          font-family: Alibaba PuHuiTi;
          font-weight: bold;
          color: #b4d3ff;
          letter-spacing: px(10);
          background: linear-gradient(
            0deg,
            #3c87ff 0%,
            #24a9e3 36.1083984375%,
            #86effe 75.3662109375%,
            #a4f3ff 100%
          );
          background-clip: text;
          -webkit-text-fill-color: transparent;
          .title-level-one {
            font-size: px(28);
          }
          .title-level-two {
            font-size: px(22);
            margin: px(8) 0 0 0;
          }
        }
      }
      main {
        margin-top: px(-60);

        .main-box {
          padding: px(70) 0 0;
          width: px(507);
          .title-card-style {
            margin: 0 auto;
          }
        }

        .list-style {
          .list-item {
            margin-bottom: px(10);
          }
        }
      }
    }
  }
}
</style>
